@import url("https://fonts.googleapis.com/css?family=Archivo");
@import url("https://fonts.googleapis.com/css?family=Anton");
@import url("https://fonts.googleapis.com/css?family=Gloria+Hallelujah");
@import url("https://fonts.googleapis.com/css?family=Roboto:300");
@import url("https://fonts.googleapis.com/css?family=Permanent+Marker");
@import url("https://fonts.googleapis.com/css?family=Press+Start+2P");

.App {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.name-section {
  align-items: flex-end;
  display: flex;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12rem;
  font-weight: 500;
  height: 100vh;
  justify-content: flex-end;
  padding-right: 30px;
  text-align: right;
}

.proj-section {
}

.proj-wrapper {
  display: grid;
  grid-gap: 30px;
  grid-template-areas:
    "proj-1 proj-2"
    "proj-3 proj-4"
    "proj-5 proj-6";
  grid-template-columns: 1fr 1fr;
  margin: 20px 10vw;
}

.proj {
  align-items: center;
  color: white;
  display: flex;
  font-size: 4rem;
  height: 16rem;
  justify-content: center;
  transition: all 0.4s ease-in-out;
}

.proj-1 {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("./images/summit-register.jpg") center no-repeat;
}

.summit-register-title {
  font-family: "Archivo", sans-serif;
}

.proj-2 {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("./images/marvelous.jpg") center no-repeat;
  font-family: "Anton", sans-serif;
  text-decoration: none;
}

.proj-3 {
  background: linear-gradient(hsla(0, 0%, 0%, 0.4), rgba(0, 0, 0, 0.4)),
    url("./images/pp.png") center no-repeat;
  font-family: "Gloria Hallelujah", cursive;
  text-decoration: none;
}

.proj-4 {
  background: #e8e8e8;
  text-decoration: none;
}

.coen-collection-title {
  color: white;
  background: url("./images/wood.jpg");
  font-weight: bold;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.proj-5 {
  background: url("./images/yellow-paper.jpg");
  color: black;
  font-family: "Permanent Marker", cursive;
  text-decoration: none;
}

.proj-6 {
  background: url("./images/space-bunny.gif") center no-repeat black;
  color: #25e200;
  font-family: "Press Start 2P", cursive;
  font-size: 3rem;
  text-decoration: none;
  text-shadow: 4px 4px red;
}

.me-section {
  background: white;
  display: grid;
  grid-template-areas:
    "headshot spotify"
    "headshot contact";
  grid-auto-columns: 1fr 1fr;
  height: 100vh;
}

.headshot {
  align-items: flex-end;
  display: flex;
  grid-area: headshot;
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5)),
    url("./images/headshotBW.jpg") center left no-repeat;
  background-size: auto 100%;
}

.text-area {
  color: white;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 2;
  padding: 0 40px;
  text-align: end;
}

.me-text {
  vertical-align: text-bottom;
}

.hello-text {
  font-size: 2.5rem;
}

.spotify {
  background: silver;
}

.contact {
}

.proj:hover {
  cursor: pointer;
  opacity: 0.4;
  transition: all 0.4s ease-in-out;
}
